import {Card, Row} from "@nextui-org/react";
import TText from "../../Common/TText";
import moment from "moment";
import {useMemo} from "react";

export default function PosterShowCard({item, size}) {

    const sizes = useMemo(() => {
        if (size === "m") {
            return {
                w: 180,
                h: 170,
                fontSize: 20
            }
        }

        return {
            w: 300,
            h: 230,
            fontSize: 30
        }
    }, [size])

    return (
        <Card variant={"flat"} isPressable css={{w: sizes.w + "px", h: sizes.h + "px", borderRadius: 0}}>
            <Card.Body css={{p: 0}}>
                <Card.Image
                    src={item.show.img}
                    width="100%"
                    height="100%"
                    objectFit="cover"
                    alt="Card example background"
                />
            </Card.Body>
            <Card.Footer css={{background: "white", borderRadius: 0, pl: 0, pr: 0}}>
                <Row justify={"space-between"}>
                    <TText size={sizes.fontSize}>{item.show.title}</TText>
                    <TText light size={sizes.fontSize} style={{color: "#8F2512"}}>{moment(item.datetime).format("HH:mm")}</TText>
                </Row>
            </Card.Footer>
        </Card>
    )
}